<template>
  <div>
    <div v-show="toggle">
      <div class="warps">
        <my-el-header />
        <div class="container">
          <div class="form-nav">
            <el-form :inline="true" :model="form" class="demo-form-inline" :rules="addFroms" ref="form">
              <el-form-item label="" prop="diaplanRateGroup">
                <el-input v-model="form.diaplanRateGroup" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="find"></el-button>
                <el-button @click="clear"></el-button>
              </el-form-item>
            </el-form>
            <el-button type="primary" @click="showAddForm(null, '')"
            >
            </el-button
            >
          </div>
          <el-dialog destroy-on-close :title="title"  :close-on-click-modal="false" :visible.sync="dialogFormVisible">
            <el-form ref="addForm" :model="addForm" :rules="addFroms">
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="groupName"
              >
                <el-input v-model="addForm.groupName" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="addForm.remark" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false"> </el-button>
              <el-button type="primary" @click="submitForm"> </el-button>
            </div>
          </el-dialog>
          <el-table height="calc(100vh - 100px - 100px - 100px - 100px)" :data="list"
                    :header-cell-style="{background:'#ccc', color: '#fff',}" style="width: 100%"
                    v-if="list.length !== 0">
            <el-table-column align="center" prop="date" label="" width="50">
              <template scope="scope">
                {{ scope.$index + 1 }}
              </template>
            </el-table-column>
            <el-table-column align="center" prop="groupName" label="">
            </el-table-column>
            <el-table-column align="center" prop="remark" label=""></el-table-column>
            <el-table-column align="center" prop="address" label="" min-width="200px" >
              <template scope="scope">
                <div class="operate">
                  <el-link type="info" @click="showAddForm(scope.row, '')"></el-link>
                  <el-link type="info" @click="isToggle(scope.row)"></el-link>
                  <template>
                    <el-popconfirm title="" @confirm="removeIt(scope.row)">
                      <el-link type="info" slot="reference">
                      </el-link>
                    </el-popconfirm>
                  </template>
                </div>
              </template>
            </el-table-column>

          </el-table>
          <el-empty v-else>
          </el-empty>
        </div>
          <my-footer v-on:next="next" @prev="prev" :form="form" @change="change" @pageCheng="pageCheng"></my-footer>
      </div>
    </div>
    <div v-show="!toggle">
      <div class="warps">
        <my-el-header />
        <div class="container">
          <div>
            <div class="form-nav">
              <div></div>
              <div class="scope">
                <el-button type="primary" @click="onSubmits"></el-button>
                <el-button type="primary" @click="black"></el-button>
              </div>
            </div>
          </div>
          <el-dialog destroy-on-close  :close-on-click-modal="false" :title="listTitle" :visible.sync="listDialogFormVisible">
            <el-form :model="addListFrom" ref="formName" :rules="addRules">
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="rateName"
              >
                <el-input v-model="addListFrom.rateName" placeholder="" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                name="rateGroup"
                required
              >
                <el-input v-model="row.groupName" placeholder="" disabled></el-input>
              </el-form-item>
              <div class="width">
                <el-form-item
                  label=""
                  :label-width="formLabelWidth"
                  prop="ratePrefix"

                >
                  <el-input
                    class="input"
                    v-model="addListFrom.ratePrefix"
                    autocomplete="off" placeholder=""
                  ></el-input>
                </el-form-item>
              </div>
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                required
              >
                <div class="width">
                  <el-form-item prop="rate">
                    <el-input style="margin-right: 20px" v-model="addListFrom.rate" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item style="margin-left: 20px" prop="billingPeriod">
                    <el-input v-model="addListFrom.billingPeriod" placeholder=""></el-input>
                  </el-form-item>
                </div>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <div class="footers">
                <p>: , 60(s)</p>
                <div class="footers">
                  <el-button @click="listDialogFormVisible = false"> </el-button>
                  <el-button type="primary" @click="submitForms('')"
                  > 
                  </el-button
                  >
                </div>
              </div>
            </div>
          </el-dialog>
          <el-table max-height="800" :data="rateItemList" style="width: 100%; margin-top: 20px"
                    v-if="list.length !== 0">
            <el-table-column align="center" prop="date" label="">
              <template scope="scope">{{ scope.$index + 1 }}</template>
            </el-table-column>
            <el-table-column align="center" prop="rateName" label="">
            </el-table-column>
            <el-table-column align="center" prop="rate" label="">
            </el-table-column>
            <el-table-column align="center" prop="billingPeriod" label="">
            </el-table-column>
            <el-table-column align="center" prop="ratePrefix" label="">
            </el-table-column>
            <el-table-column align="center" prop="createTime" label="">
            </el-table-column>
            <el-table-column prop="area" align="center" label="" fixed="right">
              <template scope="scope">
                <div class="operate">
                  <el-link type="info" @click="upDataForm(scope.row)"></el-link>
                  <template>
                    <el-popconfirm title="" @confirm="removeIt(scope.row)">
                      <el-link type="info" slot="reference">
                      </el-link>
                    </el-popconfirm>
                  </template>
                </div>
              </template>
            </el-table-column>
          </el-table>
          <my-empty v-else />
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {
  addRateItemList,
  addRateList,
  deleteRate, deleteRateItem,
  getRateItemList,
  getRateList, putRateList, upDaterateItem
} from "@/newwork/ground-colltroner";
import myEmpty from "@/newwork/myEmpty";
import { isValidNumber } from "@/util/validate";
import myFooter from "@/components/myFooter";
import myElHeader from "@/components/myElHeader";

export default {
  name: "rate",
  components: {
    myEmpty,
    myFooter,
    myElHeader
  },

  data() {
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    return {
      title: "",
      form: {
        rateGroupId: ""
      },
      /**
       *
       * 
       */
      addForm: {
        groupName: ""  //
      },
      list: [],  //
      rateItemList: [], //
      dialogFormVisible: false,
      formLabelWidth: "140px",
      toggle: true,
      shows: "",  //
      rateGroupId: "",
      /**
       * 
       */
      addList: [],
      listFrom: {
        numberPrefix: "",
        billingName: ""
      },
      listTitle: "",
      listDialogFormVisible: false,
      addListFrom: {
        rateName: "",  //
        billingPeriod: "",//
        rateGroupId: "", //ID
        ratePrefix: "", //
        rate: "" //
      },
      row: {},
      region: [
        { label: "", value: "" },
        { label: "", value: "" },
        { label: "", value: "" }
      ],
      addRules: {
        rateName: [
          { required: true, message: "", trigger: "blur" }
        ],
        ratePrefix: [
          { required: true, message: "", trigger: "blur" }
        ],
        rate: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        billingPeriod: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }

        ]
      },
      addFroms: {
        groupName: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },

  methods: {
    pageCheng(e) {
      this.form = this.$options.data().form;
      this.form.pageSize = e;
      this.getRateList(this.form);
    },
    next() {
      this.form.pageNum++;
      this.getRateList(this.form);
    },
    prev() {
      this.form.pageNum--;
      this.getRateList(this.form);
    },
    change(e) {
      this.form.pageNum = e;
      this.getRateList(this.form);
    },
    find() {
      this.getRateList(this.form);
    },
    black() {
      this.toggle = true;
    },
    clear() {
      this.form = this.$options.data().form;
      this.getRateList(this.form);
    },
    getRateList(form) {
      getRateList(form).then(res => {
        this.$bus.$emit("total", res.data.data.total);
        this.list = res.data.data.records;
      }).catch(e => {
        this.$message.error(e);
      });
    },
    /**
     *
     * 
     * @param form
     */
    getRateItemList(form) {
      getRateItemList(form).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.rateItemList = res.data.data.records;
        } else {
          console.log(res);
        }
      }).catch(e => {
        this.$message.error(e);
      });
    },
    onSubmits() {
      this.listDialogFormVisible = true;
      this.listTitle = "";
    },
    isToggle(row) {
      this.toggle = false;
      this.row = row;
      console.log(row);
      this.form.rateGroupId = row.id;
      this.getRateItemList(this.form);
    },
    /**
     *
     * 
     */
    submitForm() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.title === "") {
            addRateList(this.addForm).then(res => {
              console.log(res);
              this.getRateList(this.form);
              this.addForm = this.$options.data().addForm;
              location.reload();
            });
            this.getRateList(this.form);
            this.dialogFormVisible = false;
          } else {
            putRateList(this.addForm).then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.getRateList();
                this.$message.success("");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        }
      });
    },
    //
    submitForms() {
      this.$refs.formName.validate((valid) => {
        if (valid) {
          if (this.listTitle === "") {
            addRateItemList(this.addListFrom).then(res => {
              this.resetForm();
              if (res.data.code === 200) {
                this.$message.success("");
                this.dialogFormVisible = false;
                this.getRateItemList(this.form);
              } else {
                this.$message.error(res.data.msg);
              }
            }).catch(e => {
              this.$message.error(e);
            });
          } else {
            upDaterateItem(this.addListFrom).then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.getRateItemList(this.form);
                this.listDialogFormVisible = false;
                this.$message.success("");

              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    showAddForm(row, title) {
      this.title = title;
      if (title === "") {
        this.addForm = row;
      }
      this.dialogFormVisible = true;
    },
    //

    upDataForm(row) {
      this.addListFrom = row;
      this.listTitle = "";
      this.listDialogFormVisible = true;
    },
    //
    removeIt(row) {
      this.toggle ? deleteRate(row.id).then(res => {
        if (res.data.code == 200) {
          this.$message.success("");
          this.getRateList(this.form);
        } else {
          this.$message.error(res.data.msg);
        }
      }).catch(e => this.$message.error(e)) : deleteRateItem(row.id).then(res => {
        if (res.data.code === 200) {

          this.$message.success("");
          //formid ok
          this.getRateItemList(this.form);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    //  
    resetForm(type) {
      type === "form" ? this.$refs.form.resetFields() : this.$refs.formName.resetFields();
    }
  },
  watch: {
    listDialogFormVisible(val) {
      if (val !== true) {
        this.getRateItemList(this.form);
        this.resetForm();
      }
    },
    toggle(val) {
      if (val === true) {
        this.rateItemList = [];
      } else {
        this.addListFrom.rateGroupId = this.row.id;
      }
    }
  },
  computed: {
    createId() {
      return (this.addForm.id = (Math.random() * 1000000).toFixed(0));
    }
  },
  created() {
    this.getRateList(this.form);
    this.getRateItemList(this.form);
  }
};
</script>

<style scoped>
.a-link {
  margin-right: 10px;
}

.footers {
  display: flex;
  justify-content: space-between;
}

.footers p {
  color: #cccccc;
}

.scope {
  text-align: right;
}
.width {
  display: flex;
  width: 100%;
}

.width .input {
  width: 193px;
}
</style>
